  transition: width 0.5s ease;
  box-shadow: inset 0 2px 4px rgba(255, 255, 255, 0.2);

  &.hp {
    background: linear-gradient(90deg, #ff4444, #ff6666);
  }

  &.mp {
    background: linear-gradient(90deg, #4444ff, #6666ff);
  }

  &.sanity {
    background: linear-gradient(90deg, #fff9c4, #fff59d);
  }

  &.exp {
    background: linear-gradient(90deg, #e1bee7, #ce93d8);
  }
}

.health-value {
  font-size: 14px;
  text-align: right;
  color: #e0e0e0;
  font-weight: 500;
}

.health-description {
  margin-top: auto;
  padding-top: 16px;
  border-top: 1px solid rgba(192, 192, 192, 0.3);
  display: flex;
  align-items: flex-start;
  gap: 10px;

  i {
    font-size: 18px;
    color: #c0c0c0;
    margin-top: 2px;
  }

  p {
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    color: #e0e0e0;
    flex: 1;
  }
}

// ==================== 物品栏样式 ====================
.inventory-panel {
  display: flex;
  flex-direction: column;
  gap: 0;
  flex: 1; // 填充父容器
  min-height: 0; // 关键：确保可以正确计算 flex 子元素的高度
  overflow: hidden; // 防止内容溢出
}

.character-selector-header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 16px;
  padding-bottom: 12px;
  border-bottom: 2px solid rgba(192, 192, 192, 0.5);
}

.weight-wallet-container {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
  flex-wrap: wrap;
}

.weight-display {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
  min-width: 200px;
  padding: 10px 12px;
  background-color: rgba(0, 0, 0, 0.2);
  border: 2px solid rgba(192, 192, 192, 0.3);
  border-radius: 6px;
}

.weight-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #ffffff;
  white-space: nowrap;
  flex-shrink: 0;

  i {
    font-size: 16px;
    color: #c0c0c0;
  }
}

.weight-bar-container {
  flex: 1;
  height: 20px;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid rgba(192, 192, 192, 0.3);
  min-width: 0;
}

.weight-bar-fill {
  height: 100%;
  border-radius: 10px;
  transition: width 0.5s ease;
  box-shadow: inset 0 2px 4px rgba(255, 255, 255, 0.2);
  background: linear-gradient(90deg, #4a90e2, #5ba3f5);

  &.weight-overload {
    background: linear-gradient(90deg, #ff4444, #ff6666);
    animation: pulse 1.5s ease-in-out infinite;
  }
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

.weight-value {
  font-size: 14px;
  font-weight: 600;
  color: #e0e0e0;
  white-space: nowrap;
  flex-shrink: 0;
  min-width: 80px;
  text-align: right;
}

.wallet-display {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 0 0 auto;
  padding: 10px 12px;
  background-color: rgba(0, 0, 0, 0.2);
  border: 2px solid rgba(192, 192, 192, 0.3);
  border-radius: 6px;
  min-width: 180px;
}

.wallet-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #c0c0c0;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(192, 192, 192, 0.2);

  i {
    font-size: 16px;
    color: #d4af37; // 金色图标
  }
}

.wallet-currencies {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.currency-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;

  i {
    font-size: 14px;
    flex-shrink: 0;
  }

  .currency-label {
    font-weight: 600;
    color: #c0c0c0;
    min-width: 40px;
    flex-shrink: 0;
  }

  .currency-value {
    font-weight: 700;
    flex: 1;
    text-align: right;
  }

  &.currency-gold {
    i {
      color: #d4af37; // 金色
    }

    .currency-value {
      color: #d4af37; // 金色
    }
  }

  &.currency-silver {
    i {
      color: #c0c0c0; // 银色
    }

    .currency-value {
      color: #c0c0c0; // 银色
    }
  }

  &.currency-copper {
    i {
      color: #cd7f32; // 铜色
    }

    .currency-value {
      color: #cd7f32; // 铜色
    }
  }
}

.selector-label {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  white-space: nowrap;
  flex-shrink: 0;

  i {
    font-size: 24px;
    color: #c0c0c0;
  }
}

.character-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  flex: 1;
  overflow-x: auto;
  overflow-y: auto;
  max-height: 120px;
  padding-bottom: 4px;
  padding-right: 4px;

  // 自定义滚动条样式
  &::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.2);
    border-radius: 3px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(192, 192, 192, 0.5);
    border-radius: 3px;

    &:hover {
      background: rgba(192, 192, 192, 0.7);
    }
  }
}

.character-button {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 16px;
  background-color: rgba(0, 0, 0, 0.2);
  border: 2px solid rgba(192, 192, 192, 0.3);
  border-radius: 6px;
  color: #c0c0c0;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;

  i {
    font-size: 14px;
    color: #c0c0c0;
    transition: color 0.2s ease;
  }

  &:hover {
    background-color: rgba(0, 0, 0, 0.3);
    border-color: rgba(192, 192, 192, 0.5);
    color: #e0e0e0;

    i {
      color: #e0e0e0;
    }
  }

  &.active {
    background-color: rgba(192, 192, 192, 0.2);
    border-color: #c0c0c0;
    color: #ffffff;
    font-weight: 600;

    i {
      color: #ffffff;
    }
  }
}

.inventory-layout {
  display: flex;
  gap: 20px;
  flex: 1;
  min-height: 0; // 关键：确保可以正确计算 flex 子元素的高度
  overflow: hidden; // 防止内容溢出
  // 固定高度，与主要信息标签页的内容区域高度一致
  // 使用 flex: 1 和 min-height: 0 确保高度固定
}

.inventory-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
  min-height: 0; // 关键：确保可以正确计算高度
  // 固定高度为5个物品的高度：每个物品约68px（12px padding * 2 + 40px 图标 + 内容），5个物品 + 4个间距(8px) = 5 * 68 + 4 * 8 = 340 + 32 = 372px
  height: calc(5 * 68px + 4 * 8px); // 固定高度，确保始终显示5个物品的高度
  overflow-y: auto;
  overflow-x: hidden;
  // 自定义滚动条样式
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(192, 192, 192, 0.5);
    border-radius: 4px;

    &:hover {
      background: rgba(192, 192, 192, 0.7);
    }
  }
}

.inventory-items {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-right: 4px; // 减少右侧内边距，为滚动条留出空间
  // 不设置 flex: 1，让内容自然高度，超出部分由父容器滚动
}

.inventory-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 60px 20px;
  color: #c0c0c0;
  text-align: center;
  flex: 1; // 填充父容器
  min-height: 0; // 确保可以正确计算高度

  i {
    font-size: 48px;
    color: rgba(192, 192, 192, 0.5);
  }

  p {
    margin: 0;
    font-size: 16px;
    color: #c0c0c0;
  }
}

.inventory-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background-color: rgba(0, 0, 0, 0.2);
  border: 2px solid rgba(192, 192, 192, 0.3);
  border-radius: 8px;
  transition: all 0.2s ease;
  cursor: pointer;

  &:hover {
    background-color: rgba(0, 0, 0, 0.3);
    border-color: rgba(192, 192, 192, 0.5);
  }

  &.active {
    background-color: rgba(192, 192, 192, 0.15);
    border-color: #c0c0c0;
    box-shadow: 0 2px 8px rgba(192, 192, 192, 0.2);
  }
}

.item-icon {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(192, 192, 192, 0.1);
  border-radius: 6px;
  border: 1px solid rgba(192, 192, 192, 0.2);
  flex-shrink: 0;
  transition: all 0.2s ease;

  i {
    font-size: 20px;
    color: #c0c0c0;
    transition: color 0.2s ease;
  }

  // 稀有度颜色
  &.rarity-common {
    border-color: #8b7355;
    background-color: rgba(139, 115, 85, 0.2);

    i {
      color: #8b7355;
    }
  }

  &.rarity-uncommon {
    border-color: #5b8a72;
    background-color: rgba(91, 138, 114, 0.2);

    i {
      color: #5b8a72;
    }
  }

  &.rarity-rare {
    border-color: #3a5f8c;
    background-color: rgba(58, 95, 140, 0.2);

    i {
      color: #3a5f8c;
    }
  }

  &.rarity-epic {
    border-color: #6a0dad;
    background-color: rgba(106, 13, 173, 0.2);

    i {
      color: #6a0dad;
    }
  }

  &.rarity-legendary {
    border-color: #d4af37;
    background-color: rgba(212, 175, 55, 0.2);

    i {
      color: #d4af37;
    }
  }

  &.rarity-unique {
    border-color: #b222dc;
    background-color: rgba(178, 34, 220, 0.2);

    i {
      color: #b222dc;
    }
  }

  &.rarity-mythic {
    border-color: #ffd700;
    background: linear-gradient(135deg, #ff0000, #ffd700);
    box-shadow: 0 0 8px rgba(255, 215, 0, 0.5);

    i {
      color: #ffffff;
      text-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
    }
  }
